//@flow
import chai from 'chai';
import objectReplace from './objectReplace';

const expect = chai.expect;

describe('objectReplace', function() {

  it('should copy all properties like objectAssign does', function() {
    const source = {
      hello: 'world',
      hola: 'mundo',
      target: 2
    };

    const target = {
      target: 1
    };

    const expected = source;

    objectReplace(target, source);
    expect(target).to.deep.equal(expected);
  });

  it('should prune any properties that dont exist in the source', function() {
    const source = {
      hello: 'world',
      hola: 'mundo'
    };

    const target = {
      target: 1
    };

    const expected = source;

    objectReplace(target, source);

    expect(target).to.deep.equal(expected);
  });

});
